import { useToast } from 'vue-toastification/composition'
import ToastificationContent from '@/@core/components/toastification/ToastificationContent.vue'
import DriverService from '@/services/DriverService'
import { reactive, ref } from '@vue/composition-api'

const useDriver = () => {
  const toast = useToast()
  const isSaving = ref(false)
  const isSavingPhoto = ref(false)

  const isLoading = ref(false)
  const blank = {
    id: '',
    name: '',
    lastName: '',
    license: '',
    cellphone: '',
    photo: '',
    status: true,
    file: null,
  }

  const states = reactive({
    license: {
      isInvalid: false,
      message: '',
    },
  })

  const isSidebarActive = ref(false)

  const driver = reactive(blank)

  const resetData = () => {
    driver.id = ''
    driver.name = ''
    driver.lastName = ''
    driver.license = ''
    driver.cellphone = ''
    driver.photo = ''
    driver.status = true
    driver.file = null

    states.license.isInvalid = false
    states.license.message = ''
  }

  const saveNew = async () => {
    isSaving.value = true
    try {
      const { id, file, ...rest } = driver
      await DriverService.create(rest)
      isSaving.value = false
      isSidebarActive.value = false
      setTimeout(() => {
        resetData()
      }, 1000)
      toast({
        component: ToastificationContent,
        props: {
          title: 'Registrado correctamente!!',
          icon: 'CheckCircleIcon',
          variant: 'success',
        },
      })
      return true
    } catch (error) {
      isSaving.value = false
      if (error.response.data.statusCode === 400) {
        states.license.isInvalid = true
        states.license.message = error.response.data.message
      }
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al Actualizar',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
      throw error
    }
  }

  const edit = async id => {
    isLoading.value = true
    try {
      const response = await DriverService.getById(id)
      const {
        name,
        lastName,
        license,
        cellphone,
        photo,
        status,
      } = response.data

      driver.id = response.data.id
      driver.name = name
      driver.lastName = lastName
      driver.license = license
      driver.cellphone = cellphone
      driver.photo = photo
      driver.status = status
      isLoading.value = false
      isSidebarActive.value = true
    } catch (error) {
      isLoading.value = false
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al Consultar registro',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
      throw error
    }
  }

  const update = async () => {
    isSaving.value = true
    try {
      const { id, file, ...rest } = driver
      await DriverService.update(driver.id, rest)
      isSaving.value = false
      isSidebarActive.value = false
      setTimeout(() => {
        resetData()
      }, 1000)
      toast({
        component: ToastificationContent,
        props: {
          title: 'Actualizado Correctamente!',
          icon: 'CheckCircleIcon',
          variant: 'success',
        },
      })
    } catch (error) {
      isSaving.value = false
      if (error.response.data.statusCode === 400) {
        states.license.isInvalid = true
        states.license.message = error.response.data.message
      }
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al Actualizar',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
      throw error
    }
  }

  const save = async () => {
    if (driver.id !== '') {
      await update()
    } else {
      await saveNew()
    }
  }

  const savePhoto = async () => {
    isSavingPhoto.value = true
    try {
      const {
        id,
        file,
        photo,
        ...rest
      } = driver
      const form = new FormData()
      if (file) {
        form.append('file', file)
      }
      const response = await DriverService.uploadPhoto(form)
      await DriverService.update(driver.id, { photo: response.data.secureUrl, ...rest })
      isSavingPhoto.value = false
      isSidebarActive.value = false
      toast({
        component: ToastificationContent,
        props: {
          title: 'Actualizado Correctamente!',
          icon: 'CheckCircleIcon',
          variant: 'success',
        },
      })
    } catch (error) {
      isSavingPhoto.value = false
      if (error.response.data.statusCode === 400) {
        states.license.isInvalid = true
        states.license.message = error.response.data.message
      }
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al Actualizar foto',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
      throw error
    }
  }

  return {
    isSaving,
    isSavingPhoto,
    isSidebarActive,
    states,
    driver,
    // methods
    save,
    resetData,
    edit,
    update,
    savePhoto,
  }
}
export default useDriver
