import { useToast } from 'vue-toastification/composition'
import ToastificationContentVue from '@/@core/components/toastification/ToastificationContent.vue'
import { ref, reactive, watch } from '@vue/composition-api'
import _ from 'lodash'
import DriverService from '@/services/DriverService'

const useDriversTable = (perPage = 10) => {
  const title = 'Conductores'
  const toast = useToast()
  const isLoading = ref(false)
  const drivers = ref([])

  const perPageOptions = [10, 25, 50]

  const headerColumns = [
    { key: 'id', label: '#', sortable: true },
    { key: 'name', label: 'Nombre', sortable: true },
    {
      key: 'license',
      label: 'Licencia',
      sortable: true,
      stickyColumn: true,
      isRowHeader: true,
    },
    {
      key: 'cellphone',
      label: 'Celular',
      sortable: true,
      stickyColumn: true,
      isRowHeader: true,
    },
    {
      key: 'unit',
      label: 'Unidad',
      sortable: false,
      stickyColumn: true,
      isRowHeader: true,
    },
    {
      key: 'createdAt',
      label: 'Fecha de creación',
      sortable: true,
      stickyColumn: true,
      isRowHeader: true,
    },
    {
      key: 'updatedAt',
      label: 'Fecha de modificación',
      sortable: true,
      stickyColumn: true,
      isRowHeader: true,
    },
    { key: 'status', label: 'Estatus', sortable: true },
    { key: 'actions', label: 'Acciones' },
  ]

  const listQuery = reactive({
    take: perPage,
    page: 1,
    order: 'ASC',
    pageCount: 1,
    itemCount: 0,
    search: '',
    sortBy: 'id',
    from: 0,
    to: 0,
  })

  const fetchList = async () => {
    isLoading.value = true
    try {
      const response = await DriverService.fetchList(listQuery)
      const { data, meta } = response.data
      const {
        take,
        page,
        pageCount,
        itemCount,
        from,
        to,
      } = meta
      listQuery.itemCount = Number(itemCount)
      listQuery.pageCount = Number(pageCount)
      listQuery.take = Number(take)
      listQuery.from = Number(from)
      listQuery.to = to > data.length ? itemCount : to
      listQuery.page = page
      drivers.value = data
      isLoading.value = false
    } catch (error) {
      isLoading.value = false
      toast({
        component: ToastificationContentVue,
        props: {
          title: `Error al cargar ${title}`,
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
    }
  }

  const resolveStatus = status => (status ? 'success' : 'danger')

  const rowClass = (item, type) => {
    let colorClass = ''
    if (item || type === 'row') {
      if (item.unit !== null) {
        colorClass = 'table-success'
      } else {
        colorClass = 'table-danger'
      }
      return colorClass
    }
    return colorClass
  }

  const reload = _.debounce(() => {
    fetchList()
  }, 500)

  watch(() => listQuery.take, () => fetchList())

  watch(() => listQuery.page, () => fetchList())

  watch(() => listQuery.search, () => {
    reload()
  })

  fetchList()

  return {
    title,
    perPageOptions,
    drivers,
    headerColumns,
    isLoading,
    listQuery,
    resolveStatus,
    fetchList,
    rowClass,
  }
}
export default useDriversTable
