<template>
  <b-sidebar
    id="sidebar"
    sidebar-class="sidebar-lg"
    :visible="isSidebarActive"
    bg-variant="white"
    shadow
    backdrop
    no-header
    right
    @hidden="()=> {
      resetForm(),
      resetData()
    }"
    @change="(val) => isSidebarActive = val"
  >
    <template #default="{ hide }">
      <div
        class="d-flex justify-content-between align-items-center content-sidebar-header px-2 py-1"
      >
        <h5 class="mb-0">
          {{ driver.id ? "Editar" : "Registrar" }} Conductor
        </h5>

        <feather-icon
          class="ml-1 cursor-pointer"
          icon="XIcon"
          size="16"
          @click="hide"
        />
      </div>

      <b-row
        v-if="driver.id"
        class="d-flex align-items-center justify-content-center mt-1"
      >
        <b-col
          class="d-flex justify-content-center"
          cols="12"
          sm="12"
          md="12"
          lg="12"
          xl="12"
        >
          <!-- Media -->
          <b-media class="mb-2">
            <template #aside>
              <b-avatar
                ref="previewEl"
                variant="light"
                :src="driver.photo"
                size="150px"
                rounded
              />
            </template>
          </b-media>
        </b-col>

        <b-col
          class="d-flex justify-content-center mt-0"
          cols="12"
          sm="12"
          md="12"
          lg="12"
          xl="12"
        >
          <b-button
            variant="secondary"
            class="mr-1"
            @click="$refs.refInputEl.click()"
          >
            <input
              ref="refInputEl"
              type="file"
              class="d-none"
              accept="image/*"
              @input="inputImageRenderer"
            >
            <span class="d-none d-sm-inline">Cambiar</span>
            <feather-icon
              icon="EditIcon"
              class="d-inline d-sm-none"
            />
          </b-button>
          <b-button
            variant="success"
            class="ml-1"
            @click="uploadImage"
          >
            <feather-icon
              icon="SaveIcon"
              class="mr-50 mt-25"
            />
            <b-spinner
              v-if="isSavingPhoto"
              small
            />
            <span>Guardar Foto</span>
          </b-button>
        </b-col>
      </b-row>

      <!-- BODY -->
      <validation-observer
        #default="{ handleSubmit }"
        ref="refFormObserver"
      >
        <b-form
          ref="form"
          class="p-2"
          @submit.prevent="handleSubmit(onSubmit)"
        >
          <validation-provider
            #default="validationContext"
            name="name"
            rules="required"
          >
            <b-form-group
              :state="getValidationState(validationContext)"
              label="Nombre"
              label-for="name"
            >
              <b-form-input
                id="name"
                v-model="driver.name"
                placeholder="Nombre"
              />
              <b-form-invalid-feedback
                :state="getValidationState(validationContext)"
              >
                {{ validationContext.errors[0] }}
              </b-form-invalid-feedback>
            </b-form-group>
          </validation-provider>
          <validation-provider
            #default="validationContext"
            name="lastName"
            rules="required"
          >
            <b-form-group
              :state="getValidationState(validationContext)"
              label="Apellidos"
              label-for="lastName"
            >
              <b-form-input
                id="lastName"
                v-model="driver.lastName"
                placeholder="Apellidos"
              />
              <b-form-invalid-feedback
                :state="getValidationState(validationContext)"
              >
                {{ validationContext.errors[0] }}
              </b-form-invalid-feedback>
            </b-form-group>
          </validation-provider>
          <b-alert
            v-if="states.license.isInvalid"
            show
            variant="danger"
          >
            <div class="alert-body">
              <feather-icon
                class="mr-25"
                icon="AlertCircleIcon"
              />
              <span class="ml-25">{{ states.license.message }}</span>
            </div>
          </b-alert>
          <validation-provider
            #default="validationContext"
            name="license"
            rules="required"
          >
            <b-form-group
              :state="getValidationState(validationContext)"
              label="Licencia de conducir"
              label-for="license"
            >
              <b-form-input
                id="license"
                v-model="driver.license"
                placeholder="Número de licencia"
              />
              <b-form-invalid-feedback
                :state="getValidationState(validationContext)"
              >
                {{ validationContext.errors[0] }}
              </b-form-invalid-feedback>
            </b-form-group>
          </validation-provider>
          <validation-provider
            #default="validationContext"
            name="cellphone"
            rules="required"
          >
            <b-form-group
              :state="getValidationState(validationContext)"
              label="Celular"
              label-for="cellphone"
            >
              <b-form-input
                id="cellphone"
                v-model="driver.cellphone"
                placeholder="Celular"
              />
              <b-form-invalid-feedback
                :state="getValidationState(validationContext)"
              >
                {{ validationContext.errors[0] }}
              </b-form-invalid-feedback>
            </b-form-group>
          </validation-provider>
          <b-form-group
            label="Activo"
            label-for="status"
          >
            <b-form-checkbox
              v-model="driver.status"
              class="custom-control-success"
              name="status"
              switch
            />
          </b-form-group>
          <b-button
            v-ripple.400="'rgba(255, 255, 255, 0.15)'"
            variant="primary"
            class="mr-2 mt-4"
            type="submit"
            block
          >
            <feather-icon
              icon="SaveIcon"
              class="mr-50 mt-25"
            />
            <b-spinner
              v-if="isSaving"
              small
            />
            <span class="align-middle ml-1">
              {{ driver.id ? "Actualizar" : "Registrar " }}
            </span>
          </b-button>
        </b-form>
      </validation-observer>
    </template>
  </b-sidebar>
</template>

<script>
import { ref } from '@vue/composition-api'
import { useInputImageRenderer } from '@core/comp-functions/forms/form-utils'
import {
  BAlert,
  BAvatar,
  BCol,
  BSidebar,
  BForm,
  BFormCheckbox,
  BFormGroup,
  BFormInput,
  BFormInvalidFeedback,
  BButton,
  BMedia,
  BSpinner,
  BRow,
} from 'bootstrap-vue'
import { ValidationProvider, ValidationObserver, localize } from 'vee-validate'
import { required } from '@validations'
import formValidation from '@core/comp-functions/forms/form-validation'
import Ripple from 'vue-ripple-directive'
import useDriver from '../composables/useDriver'

localize({
  en: {
    messages: {
      // generic rule messages...
    },
    fields: {
      name: {
        required: 'Introduce un nombre',
      },
      lastName: {
        required: 'Introduce los apellidos',
      },
      license: {
        required: 'Introduce el número de licencia de conducir',
      },
      cellphone: {
        required: 'Introduce un número celular',
      },
    },
  },
})

export default {
  components: {
    BAlert,
    BAvatar,
    BCol,
    BMedia,
    BSpinner,
    BSidebar,
    BForm,
    BFormCheckbox,
    BFormGroup,
    BFormInput,
    BFormInvalidFeedback,
    BButton,
    BRow,

    // Form Validation
    ValidationProvider,
    ValidationObserver,
  },
  directives: {
    Ripple,
  },
  setup(__, { emit }) {
    const refInputEl = ref(null)
    const previewEl = ref(null)
    const resetuserData = () => emit('reset-data')

    const { refFormObserver, getValidationState, resetForm } = formValidation(resetuserData)

    const {
      driver,
      edit,
      isSaving,
      isSavingPhoto,
      isSidebarActive,
      resetData,
      save,
      savePhoto,
      states,
    } = useDriver()

    const { inputImageRenderer } = useInputImageRenderer(
      refInputEl,
      base64 => {
        const [file] = refInputEl.value.files
        driver.file = file
        driver.photo = base64
      },
    )

    const onSubmit = () => {
      save().then(() => {
        emit('reload')
      })
    }

    const open = async () => {
      isSidebarActive.value = true
    }

    const getElement = async id => {
      await edit(id)
    }

    const uploadImage = async () => {
      savePhoto().then(() => {
        emit('reload')
      })
    }

    return {
      driver,
      isSaving,
      isSavingPhoto,
      isSidebarActive,
      states,

      onSubmit,
      refFormObserver,
      getValidationState,
      resetForm,
      open,
      getElement,
      resetData,
      uploadImage,

      required,

      inputImageRenderer,
      previewEl,
      refInputEl,
    }
  },
}
</script>

<style lang="scss">
@import "@core/scss/vue/libs/vue-select.scss";

#add-new-user-sidebar {
  .vs__dropdown-menu {
    max-height: 200px !important;
  }
}
</style>
