<template>
  <div>
    <DriversTable
      ref="driversTable"
      @handle-edit="(id) => $refs.sidebarDriver.getElement(id)"
    >
      <template v-slot:plus>
        <b-button
          variant="primary"
          @click="$refs.sidebarDriver.open()"
        >
          <span class="text-nowrap">Registrar Nuevo</span>
        </b-button>
      </template>
    </DriversTable>
    <SidebarDriver
      ref="sidebarDriver"
      @reload="$refs.driversTable.fetchList()"
    />
  </div>
</template>

<script>
import { ref } from '@vue/composition-api'
import { BButton } from 'bootstrap-vue'
import DriversTable from './components/DriversTable.vue'
import SidebarDriver from './components/SidebarDriver.vue'

export default {
  components: {
    BButton,
    DriversTable,
    SidebarDriver,
  },
  setup() {
    const sidebarDriver = ref()
    const devicesTable = ref()
    return {
      devicesTable,
      sidebarDriver,
    }
  },
}
</script>
<style></style>
